@media (max-width: 768px) {
    body {
        font-size: 18.5px;
        line-height: 30px;
    }
}

@media (min-width: 768px) {
    body {
        padding: 0 1em;
    }

    .grid ul li {
        flex: 1;
    }

    .grid img {
        width: 210px;
    }

    .intro p {
        font-size: 1.4em;
    }

    .lista-card {
        position: relative;
        float: left;
        width: 50%;
    }

    .lista-card-in.card-1 {
        height: 714px;
    }

    .article-image {
        height: 350px;
    }

    .lista-card-in {
        height: 350px;
    }

    .title {
        width: 30%;
        float: left;
    }

    .desc {
        width: 70%;
        float: right;
    }

    .large {
        display: block !important;
    }

    .small {
        display: none !important;
    }
}

@media (min-width: 1024px) {
    body {
        padding: 0 4em;
    }

    .article-content {
        padding: 0 2em;
    }
}

@media (min-width: 1280px) {
    body {
        padding: 0 12em;
    }

    .grid img {
        width: 180px;
    }

    .article-content {
        padding: 0 4em;
    }
}