.clearfix::after {
    content: "";
    display: table;
    clear: both;
}

html,
body {
    background: $dark-color !important;
    height: 100%;
}

body {
    min-width: 320px;
    display: flex;
    flex-direction: column;
}

div {
    // background: inherit;
}

img {
    vertical-align: middle;
    width: 100%;
    height: auto;
}

p {
    padding: 0 0 20px;
    margin: 0;
}

.body {
    flex: 1 0 auto;
}

/* TOP */
.header div {
    margin: 3em auto;
}

.header ul {
    margin-top: 2px;
    padding-bottom: 3em;
}

.header .logo {
    float: left;
    padding: 0;
}

.logo,
.logo-home {
    position: relative;
    display: block;
    background: url("../images/varie/logo.png") no-repeat;
}

.logo {
    background-size: 42px;
    width: 42px;
    height: 42px;
}

.logo-home {
    margin: 5em auto 0;
    background-size: 84px;
    width: 84px;
    height: 84px;
}

.header-menu li {
    display: inline;
    margin-right: 1rem;
}

.header-menu li:last-child {
    margin-right: 5px;
}

.header-menu {
    float: right;
    padding: 0;
    white-space: nowrap;
    overflow-x: auto;
    display: inline-block;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
}

.header-menu::-webkit-scrollbar {
    display: none;
}

.intro {
    margin-bottom: 2em;
}

.intro p {
    font-size: 1.2em;
    line-height: 1.4em;
}

.title {
    width: 100%;
}

.desc {
    width: 100%;
}

.main,
.page,
.studio {
    background: inherit;
    width: 100%;
    padding-bottom: 3em !important;
    position: relative;
    z-index: 1 !important;
}

.studio-list {
    margin-bottom: 1.5em;
}

.studio-list h4 {
    margin-left: 7px;
    padding-bottom: 0.25rem;
}

.view-all {
    margin-left: 7px;
}

.icon {
    position: relative;
    vertical-align: -1px;
    overflow: hidden;
}

.page a {

    &:hover,
    &:focus {
        opacity: 0.9;
    }
}

.lista-card {
    width: 100%;
}

.article-image,
.lista-card-in {
    height: 240px;
    border: $border;
}

.lista-card-in {
    margin: 7px;
}

.lista-card a {

    &:hover,
    &:focus {
        opacity: 0.9;
    }
}

.lista-text {
    padding: 1.0em;
    width: 100%;
}

.article-title {
    padding: 1.0em 0;
    width: 100%;
}

.lista-text h1,
.article-title h1 {
    margin-top: -7px;
}

.article-content h1 {
    margin: 0.5em 0 0.25em;
}

.main ul,
.studio ul {
    margin-bottom: 20px;
}

.main ul li,
.studio ul li {
    padding-left: 1em;
}

// ARTICLES
.article-image {
    position: relative;
    margin: 0 auto 2em;
    width: 100%;
}


.media {
    margin: 30px 0;
}

.media img {
    display: block;
    margin: 20px auto;
    height: auto;
    transition: all 0.4s cubic-bezier(.25, .8, .25, 1);
}

.caption {
    position: relative;
    width: 100%;
    margin: 10px auto;
    text-align: center;
}

.quote {
    text-align: center;
}

.grid img {
    display: block;
    width: 100%;
    height: auto;
    transition: all 0.4s cubic-bezier(.25, .8, .25, 1);
    border: $border;
}

.grid ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.grid ul li {
    flex-basis: 100%;
    padding-left: 0 !important;
}

.grid ul li::before {
    content: "";
}

.page-link {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: -40px;
    margin-bottom: 40px;
}

.large {
    display: none !important;
}

.hide {
    display: none !important;
}

.show {
    display: block !important;
}

.not-found {
    position: relative;
    width: 100%;
    height: 22em;
    border: $border;
}

.not-found ul li h1 {
    padding-top: 1em;
}

/* FOOTER */
.footer {
    flex-shrink: 0;
    background: inherit;
    z-index: 2 !important;
    height: 2.5em;
}

.footer div {
    border-top: $border;
}

.all-articles a {
    margin-bottom: 5px;
}

.footer ul li {
    display: inline;
    float: left;
    padding: 0 0.5em 0 0;
}

.footer-right {
    position: relative;
    float: right;
}

.footer-right li:last-child {
    padding-right: 0 !important;
}

.m-details,
.m-by {
    display: none;
}

// Animations
/* Float */
.hvr-float {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.28s;
    transition-duration: 0.28s;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.hvr-float:hover,
.hvr-float:focus,
.hvr-float:active {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
}

/* Underline From Left */
.hvr-underline-from-left,
.main a {
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
}

.hvr-underline-from-left:before,
.main a::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0.45em;
    background: $link-color;
    height: 2px;
    -webkit-transition-property: right;
    transition-property: right;
    -webkit-transition-duration: 0.28s;
    transition-duration: 0.28s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.hvr-underline-from-left:hover:before,
.hvr-underline-from-left:focus:before,
.hvr-underline-from-left:active:before,
.main a:hover:before,
.main a:focus:before,
.main a:active:before {
    right: 0;
}

/* Wobble Horizontal */
@-webkit-keyframes hvr-wobble-horizontal {
    16.65% {
        -webkit-transform: translateX(8px);
        transform: translateX(8px);
    }

    33.3% {
        -webkit-transform: translateX(-6px);
        transform: translateX(-6px);
    }

    49.95% {
        -webkit-transform: translateX(4px);
        transform: translateX(4px);
    }

    66.6% {
        -webkit-transform: translateX(-2px);
        transform: translateX(-2px);
    }

    83.25% {
        -webkit-transform: translateX(1px);
        transform: translateX(1px);
    }

    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes hvr-wobble-horizontal {
    16.65% {
        -webkit-transform: translateX(8px);
        transform: translateX(8px);
    }

    33.3% {
        -webkit-transform: translateX(-6px);
        transform: translateX(-6px);
    }

    49.95% {
        -webkit-transform: translateX(4px);
        transform: translateX(4px);
    }

    66.6% {
        -webkit-transform: translateX(-2px);
        transform: translateX(-2px);
    }

    83.25% {
        -webkit-transform: translateX(1px);
        transform: translateX(1px);
    }

    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

.hvr-wobble-horizontal {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

.hvr-wobble-horizontal:hover,
.hvr-wobble-horizontal:focus,
.hvr-wobble-horizontal:active {
    -webkit-animation-name: hvr-wobble-horizontal;
    animation-name: hvr-wobble-horizontal;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

